*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root{
  --mainColor: #FF1F00
}
body{
  background-color:rgb(220, 220, 220) ;
}
.center{
  display: flex;
  margin: 0 auto;
  max-width: 1100px;
  padding: 0 2%;
}

.header{
  background-color: white;
  border-bottom: 2px solid rgb(230, 230, 230);
  padding: 10px 0;
}

.header_logo{
  width: 50%;
}
.header_loginform{
  width: 50%;
  text-align: right;
}
.header_loginform form{
  display: inline-block;
}
.header_loginform input[type =text],
.header_loginform input[type =password]{
  width: 180px;
  height: 20px;
  border: 1px solid #ccc;
  margin-right: 5px;
  padding-left: 10px;;
}
.header_loginform input[type =submit]{
  background-color: var(--mainColor);
  color: white;
  border: 0;
  cursor: pointer;
  font-size: 14px;
  padding: 0px 10px;
  height: 20px;
  border-radius: 10px;
}
.btn_criarConta{
  display: inline-block;
  font-size: 14px;
  margin: 0 10px;
}
.btn_criarConta a{
  color:white;
  background-color: var(--mainColor);
  text-decoration: none;
  padding: 0 3px;
  cursor: pointer;
  border-radius: 10px;
  
}
.header_logadoInfo{
  text-align: right;
  width: 50%;
}
.header_logadoInfo a{ 
  margin: 0 10px;
  background-color: var(--mainColor);
  color: white;
  text-decoration: none;
  padding: 0 10px;
  cursor: pointer;
  border-radius: 10px;
  font-size: 14px; 
}
.header_logadoInfo strong{
  color: #A80400;
}
.modalCriarConta{
  display: none;
  position: fixed;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
}
.formCriarConta{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  background-color: white;
  padding: 20px 90px;
  width: 80%;
  max-width: 700px;
  text-align: center;
}
.formCriarConta input{
  width: 100%;
  height: 30px;
  padding-left: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
}
.formCriarConta input[type='submit']{
  background-color: var(--mainColor);
  cursor: pointer;
  color: white;

}
.close-modal-criar{
  position: absolute;
  right:-10px;
  top:-10px;
  cursor: pointer;
  width: 30px;
  text-align: center;
  color: white;
  line-height: 30px;
  height: 30px;
  border-radius: 15px;
  background-color: var(--mainColor);
}
.modalUpload{
  display: none;
  position: fixed;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
}
.formUpload{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  background-color: white;
  padding: 20px 90px;
  width: 80%;
  max-width: 700px;
  text-align: center;
}
.formUpload input{
  width: 100%;
  height: 30px;
  padding-left: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
}
.formUpload input[type='submit']{
  background-color: var(--mainColor);
  cursor: pointer;
  color: white;

}
.close-modal-criar{
  position: absolute;
  right:-10px;
  top:-10px;
  cursor: pointer;
  width: 30px;
  text-align: center;
  color: white;
  line-height: 30px;
  height: 30px;
  border-radius: 15px;
  background-color: var(--mainColor);
}
.postSingle{
  background-color: white;
  max-width: 700px;
  margin: 10px auto;
}
.postSingle img{
  width: 100%;
}
.postSingle p{
  padding: 10px;
}
.postSingle p strong{
  color: #A80400;
}
.postSingle form{
  padding: 20px;
}
.postSingle textarea{
  width: 100%;
height: 120px;
border: 1px solid #FFBF00;
padding: 10px;
}
.postSingle textarea:focus{
  outline: solid #FFBF00;
}
.postSingle input[type=submit]{
  background-color: var(--mainColor);
  cursor: pointer;
  color: white;
  border: 0;
  padding: 8px 10px;
}
.coments{
  margin-left: 20px;
}
.coments strong{
  color: var(--mainColor)!important;
}
.coments h2{
  border-bottom: 1px solid #FFBF00;
  padding: 8px 0;
  color: #FF8200;
}

.coments-single p{
  font-size: 14px;
}
